.dropdown {
  position: relative;
}

.dropdown .menu {
  display: none;
  position: absolute;
  right: 0;
  top: 100%;
  transform: translate(32px, 18px);
  z-index: 1;
}

.dropdown.is-active::after {
  border-color: transparent transparent #333333;
  border-style: solid;
  border-width: 0 12px 12px;
  content: "";
  height: 0;
  position: absolute;
  right: 0;
  transform: translate(5px, 9px);
  width: 0;
  z-index: 1;
}

.dropdown.is-active .menu {
  display: block;
}

